'use client';

import { useParams, useRouter } from 'next/navigation';
import Image from 'next/image';
import Link from 'next/link';
import { products } from '@/data/products';
import { getAverageRating, getReviewCount } from '@/data/reviews';
import ReviewsDisplay from '@/components/ReviewsDisplay';

interface Product {
  id: string;
  name: string;
  description: string;
  category: string;
  image: string;

  tags?: string[];
  benefits?: string[];
  ingredients?: string[];
  storageInfo?: string;
  shelfLife?: string;
}

const getProductDetails = (id: string): Product | undefined => {
  return products.find(product => product.id === id);
};

export default function ProductDetail() {
  const params = useParams();
  const router = useRouter();
  const id = typeof params.id === 'string' ? params.id : '';
  const product = getProductDetails(id);
  
  // Get review data for this product
  const averageRating = getAverageRating(id);
  const reviewCount = getReviewCount(id);

  // Star Rating Component
  const StarRating = ({ rating, size = 'sm', showNumber = false }: { rating: number; size?: 'sm' | 'md'; showNumber?: boolean }) => {
    const sizeClasses = {
      sm: 'w-4 h-4',
      md: 'w-5 h-5'
    };

    return (
      <div className="flex items-center gap-2">
        <div className="flex items-center">
          {[1, 2, 3, 4, 5].map((star) => (
            <svg
              key={star}
              className={`${sizeClasses[size]} ${star <= Math.floor(rating) ? 'text-yellow-400' : 'text-gray-300'}`}
              fill="currentColor"
              viewBox="0 0 20 20"
            >
              <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
            </svg>
          ))}
        </div>
        {showNumber && (
          <span className="text-sm text-gray-600 ml-1">
            {rating.toFixed(1)} out of 5 stars
          </span>
        )}
      </div>
    );
  };

  const handleBackToProducts = (e: React.MouseEvent) => {
    e.preventDefault();
    
    // Navigate to home page
    router.push('/');
    
    // Wait for navigation and then scroll to products section
    setTimeout(() => {
      const productsSection = document.getElementById('all-products');
      const navbar = document.querySelector('nav');
      
      if (productsSection && navbar) {
        const navbarHeight = navbar.getBoundingClientRect().height;
        const elementPosition = productsSection.getBoundingClientRect().top + window.pageYOffset;
        const offsetPosition = elementPosition - navbarHeight - 16;
        
        window.scrollTo({
          top: offsetPosition,
          behavior: 'smooth'
        });
      }
    }, 500);
  };

  if (!product) {
    return (
      <div className="min-h-screen flex flex-col items-center justify-center p-4">
        <h1 className="text-2xl font-bold text-gray-800 mb-4">Product not found</h1>
        <Link 
          href="/products" 
          className="text-orange-600 hover:text-orange-700 font-medium"
        >
          Back to Products
        </Link>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto px-4 py-8 sm:py-12">
      {/* Back button */}
      <div className="mb-8">
        <Link
          href="/"
          className="inline-flex items-center text-orange-600 hover:text-orange-700 font-medium group transition-transform duration-200 ease-in-out transform hover:-translate-x-1"
          onClick={handleBackToProducts}
        >
          <svg 
            className="w-5 h-5 mr-2 transition-transform duration-200 ease-in-out transform group-hover:translate-x-1" 
            fill="none" 
            stroke="currentColor" 
            viewBox="0 0 24 24"
          >
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M10 19l-7-7m0 0l7-7m-7 7h18" />
          </svg>
          Back to Products
        </Link>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-8 lg:gap-12">
        {/* Product Image */}
        <div className="relative h-[400px] md:h-[500px] rounded-lg overflow-hidden shadow-lg">
          <Image
            src={product.image || '/products/placeholder.jpg'}
            alt={product.name}
            fill
            className="object-cover"
            sizes="(max-width: 768px) 100vw, 50vw"
            priority
          />
        </div>

        {/* Product Details */}
        <div className="flex flex-col">
          <div className="mb-6">
            <span className="inline-block px-3 py-1 text-sm bg-orange-100 text-orange-600 rounded-full mb-4">
              {product.category}
            </span>
            <h1 className="text-2xl sm:text-3xl font-bold text-gray-800 mb-4">
              {product.name}
            </h1>
            
            {/* Rating Section */}
            {reviewCount > 0 && (
              <div className="flex items-center gap-4 mb-4">
                <StarRating rating={averageRating} size="md" showNumber />
                <button 
                  onClick={() => {
                    const reviewsSection = document.querySelector('#customer-reviews');
                    reviewsSection?.scrollIntoView({ behavior: 'smooth' });
                  }}
                  className="text-orange-600 hover:text-orange-700 text-sm font-medium underline"
                >
                  ({reviewCount} customer review{reviewCount !== 1 ? 's' : ''})
                </button>
              </div>
            )}
            
            <p className="text-lg text-gray-600 mb-4">{product.description}</p>
          </div>

          {/* Benefits */}
          {product.benefits && product.benefits.length > 0 && (
            <div className="mb-6">
              <h2 className="text-xl font-semibold text-gray-800 mb-3">Benefits</h2>
              <ul className="space-y-2">
                {product.benefits.map((benefit, index) => (
                  <li key={index} className="flex items-start">
                    <span className="text-orange-500 mr-2">•</span>
                    <span className="text-gray-600">{benefit}</span>
                  </li>
                ))}
              </ul>
            </div>
          )}

          {/* Ingredients */}
          {product.ingredients && (
            <div className="mb-6">
              <h2 className="text-xl font-semibold text-gray-800 mb-3">Ingredients</h2>
              <div className="flex flex-wrap gap-2">
                {product.ingredients.map((ingredient, index) => (
                  <span 
                    key={index}
                    className="px-3 py-1 bg-gray-100 text-gray-700 rounded-full text-sm"
                  >
                    {ingredient}
                  </span>
                ))}
              </div>
            </div>
          )}

          {/* Storage and Shelf Life */}
          <div className="mt-auto space-y-4 border-t border-gray-200 pt-6">
            {product.storageInfo && (
              <div>
                <h2 className="text-lg font-semibold text-gray-800 mb-2">Storage Information</h2>
                <p className="text-gray-600">{product.storageInfo}</p>
              </div>
            )}
            {product.shelfLife && (
              <div>
                <h2 className="text-lg font-semibold text-gray-800 mb-2">Shelf Life</h2>
                <p className="text-gray-600">{product.shelfLife}</p>
              </div>
            )}
          </div>

          {/* Contact Button */}
          <div className="mt-8">
            <Link
              href="#contact"
              className="inline-block bg-orange-600 text-white px-6 py-3 rounded-lg font-medium hover:bg-orange-700 transition-colors duration-300"
            >
              Contact for Price
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}
